

.border-top{
  width:100%;
  height:7vh;
  background-color: #000000;
  position: fixed;
  top: 0px;
  z-index: 100;
}


.border-bottom{
  width:100%;
  height:7vh;
  background-color: #000000;
  position: fixed;
  bottom: 0px;
  z-index: 100;
}





.contentcontainer{
  width: 100vw;
  height: 86vh;
  margin: 0;
  /* padding: 10px; */
  position: absolute;
  /* overflow-y: scroll; */
  overflow-x: hidden;
}


.content-center{
  max-width: 1200px;
  width: 90vw;
  min-height: 100%;
  margin: 0 auto;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.content-center p{
  font-size: 15px;
  line-height: 20px;
  
}

.content-center-inner{
  padding-top: 18vh;
  min-height: 100%;
}

.content-center-left{
  width: 45%;
  min-height: 100%;
  
  /* border: 1px solid #61dafb; */
  float: left;
}

.content-center-right{
  margin: 7vh 0 0 0;
  padding: 0 0 0 0;
  width: 54%;
  min-height: 86vh;
  display: inline-block;
  /* border: 10px solid #cccccc; */
  position:fixed;
  right: -1vw;
  top: 0;
}





#copyright{
  position: relative;
  max-width: 300px;
  color: #cccccc;
  margin: 3vh auto 0 auto;
  text-align: center;
  font-size: 14px;
}

#copyright p{
  margin: 0px;
  opacity: .3;
}
#copyright p:hover{
  color: #ffffff !important;
  opacity: 1;
}












.App {
  height: 100%;
  padding-top: 7vh;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}












































@media screen and (max-width: 600px) and (min-width: 300px) {


  
.content-center{
  max-width: 1200px;
  width: 100vw;
  min-height: 100%;
  margin: 0 auto;
  text-align: center;
  overflow-y: none;
  position: relative;
}



  .content-center-inner{
    border: 1px solid #61dafb;
    padding-top: 0;
  }

  .content-center-left{
    width: 100%;
    height: 60%;
    padding: 16vh 10px 0 10px;
    /* display: none; */
    /* border: 1px solid #61dafb; */
    float: left;
  }
  
  .content-center-right{
    margin: 0;
    padding: 0 0 0 0;
    width:100%;
    min-height: 55vh;
    display: block;
    /* border: 10px solid #cccccc; */
    overflow: hidden;
    position:relative;
    top: 0;
  }
  

  .skill-box{
    width: 100%;
    margin: 10px 0;
    height: 90px;
    border-radius: 10px;
    background-color: #cccccc;
    display:block;
    float: none;
    text-align: right !important;

}

  .skill-box p{
    position: absolute;
    text-align: center !important;
    bottom: 0;
    margin: 0;
    padding: 0;
}

