.whatWeDo-title{
    font-family: "Poppins Black";
    font-size:5em;
    font-weight: 900;
    color:#1e1e1e;
    margin: 0 0 10px 0;
    padding: 0;
    line-height: 55px;
    text-align: right;
}



.WWD_nav{
    margin: 0;
    padding: 0 0 10px  0;
    text-align: right; 
}

.WWD_nav li{
    display: inline-block;
    padding: 3px 5px;
    /* border: 1px dashed #1e1e1e; */
    font-weight: 500;
}

.WWD_nav li:hover{
    cursor: pointer;
    color:#228d91;
}



.tile{
    width: 10%;
    height: 150px;
    background-color: #272727;
    margin: 0 2.5px;
    display:inline-block;
    border: 1px solid rgba(0,0,0,.3);
}

.tile:hover{
    width: 40%;
    cursor: pointer;
}





.whatWeDo-copy-container{
    padding-left: 40px;
    text-align: left;
}


.skill-box{
    width: 130px;
    margin: 4px;
    height: 90px;
    border-radius: 6px;
    position: relative;
    display: inline-block;
    text-align: center;
}


.skill-box:hover{
    background-color: #228d91;
    color: #ffffff;
    
}



.visit-site-btn{
    width: 225px;
    height: 50px;
    font-size: 1.5em;
    /* padding: 10px 20px; */
    border: 2px solid #c0c0c0;
    color: #c0c0c0;
    border-radius: 40px;
    margin: 40% auto auto auto;
    inset: 0rem;
    position:absolute;
    background-color: rgba(0,0,0,.7);
    transform:scale(.8);
    transition: .3s !important;
}


.visit-site-btn:hover{
    border: none;
    color: #ffffff;
    cursor:pointer;
    background-color: #228d91;
}
