.navLink {
    position: absolute;
    bottom: 5px;
    right: 40px;
    z-index: 110;
    list-style-type: none;
    font-family: 'Poppins';
  }

  .navLink li{
    display: inline-block;
    margin: 0 7px;
    
  }

  .navLink li a{
    text-decoration: none;
    font-size: 12px;
  }

  .navLink li a:hover{
    text-decoration: none;
    color: #228d91;
  }

  