
.homecontainer{
    width: 100vw;
    height: 86vh;
    margin: 0;
    /* padding: 10px; */
    position: absolute;
    overflow-y: hidden;
    overflow-x: hidden;
  }



.home_overlayBG{
    width:100vw;
    height: 100%;
    display: block;
    background-color: rgba(0,0,0,.4);
    top:0;
    z-index: 1;
}


.home_copy-container{
    width: 700px;
    max-width:40vw;
    height: 40%;
    /* border: 15px solid #ffffff; */
    margin: auto;
    inset: 0rem;
    position:fixed;
    /* background-color: rgba(0,0,0,.5); */
    /* box-shadow: 1px 10px 10px #000000; */
}

.home_copy-container img{
    width: 100%;
}


.home_title_mini{
    font-size:3em;
    text-align: center;
    font-family: "Poppins Black";
    font-weight: 100;
    color: white;
    margin: 70px 0 0 0 ;
    line-height: 100px;
    text-shadow:8px 9px #000000;
}


.home_title{
    line-height: 70px;
    font-size: 9em;
    text-align: center;
    font-family: "Poppins Black";
    color: white;
    margin: 0;
    text-shadow:2px 20px #000000;
}