.img1{
    max-width: 700px;
    width: 100%;
}


.wel-top-space{
    width: 100%;
    height: 25vh;
    display: block;
}


.wel-left{
    width: 50%;
    min-height: 100px;    
    /* border: 1px solid orange; */
    display: inline-block;
    padding: 10px;
}


.wel-left h1{
    font-family: "Poppins Black";
    font-size:6.5em;
    font-weight: 900;
    color:#1e1e1e;
    margin: 0 0 25px 0;
    padding: 0;
    line-height: 40px;
}

.wel-left h1 span{
    color: #ffffff;
    font-family: "Poppins Black";
}



.wel-left div p{
    font-size: 1.3em;
    margin-top: 20px;
    line-height: 24px;
    font-weight: 900;
    color: #1e1e1e;
    text-align: justify;
}


.wel-left h4{
    text-align: right;
    font-size: 2.5em;
    margin: -15px 0 0 0;
    padding: 0;
    font-family: "Poppins Black";
    color: #1e1e1e;
}


.wel-left h4 span{
    color: #ffffff;
}






.wel-right{
    width: 50%;
    min-height: 100px;
    /* border: 1px solid red; */
    float: right;
}


.bboard{
    position: fixed;
    right: 0;
    top: -50px;
    width: 47%;
}

.ppoint{
    position: fixed;
    left: 10vw;
    bottom: 3vh;
    width:20vw;
    max-width: 500px;
}